import Vue from "vue";
import NetworkService from '../services/network.service';
export default Vue.extend({
    name: "Register",
    data: function () {
        return {
            busy: false,
            registerForm: {
                username: '',
                password: '',
                role: 'visitor'
            },
            rules: {
                username: [
                    { required: true, message: 'Please input username', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: 'Please enter password', trigger: 'blur' }
                ],
                role: [
                    { required: true, message: 'Please select role', trigger: 'blur' }
                ]
            },
            rolesList: [
                {
                    value: 'visitor',
                    label: 'Visitor'
                },
                {
                    value: 'user',
                    label: 'User'
                },
                {
                    value: 'admin',
                    label: 'Administrator'
                }
            ]
        };
    },
    methods: {
        submitForm: function () {
            var _this = this;
            this.$refs['registerForm'].validate()
                .then(function () {
                _this.busy = true;
                NetworkService
                    .register(_this.registerForm.username, _this.registerForm.password, _this.registerForm.role)
                    .then(function () { return _this.$router.push({ name: 'Users' }); })
                    .catch(function (err) { return _this.$message.error(err.name + ": " + err.message); })
                    .then(function () { return _this.busy = false; });
            })
                .catch(function () { return console.error('register form validation fail'); });
        }
    }
});



