import axios from 'axios';
import auth from '../store/modules/auth';
import TokenService from "./token.service";
var authPath = '/auth';
var loginUrl = authPath + "/login";
var exchangeTokenUrl = authPath + "/token";
var axiosInstance = axios.create();
axiosInstance.defaults.baseURL = 'https://server.grigoblin.ru';
axiosInstance.interceptors.request.use(function (config) {
    console.log('send request', config.method, config.url);
    switch (config.url) {
        case loginUrl: {
            return config;
        }
        case exchangeTokenUrl: {
            return authorizedConfig(config);
        }
        default: {
            return TokenService.checkLifetime()
                .then(function () { return authorizedConfig(config); })
                .catch(function (error) { return Promise.reject(error); });
        }
    }
}, function (error) { return Promise.reject(error); });
function authorizedConfig(config) {
    var accessToken = auth.state.accessToken;
    if (accessToken)
        config.headers.Authorization = "Bearer " + accessToken;
    return config;
}
var NetworkService = (function () {
    function NetworkService() {
    }
    NetworkService.login = function (username, password) {
        var userAgent = navigator.userAgent;
        var data = {
            username: username,
            password: password,
            userAgent: userAgent
        };
        return axiosInstance.post(loginUrl, data)
            .then(function (response) { return auth.commitAuthorized(response.data); })
            .catch(function (err) { return Promise.reject(err); });
    };
    NetworkService.exchangeToken = function () {
        return axiosInstance.get(exchangeTokenUrl)
            .then(function (response) { return auth.commitTokenReceived(response.data); })
            .catch(function (err) { return Promise.reject(err); });
    };
    NetworkService.register = function (username, password, role) {
        var data = {
            username: username,
            password: password,
            role: role
        };
        return axiosInstance.post('/auth/register', data)
            .catch(function (err) { return Promise.reject(err); });
    };
    NetworkService.getUsers = function () {
        return axiosInstance.get('/api/users')
            .then(function (response) { return response.data.users; })
            .catch(function (err) { return Promise.reject(err); });
    };
    NetworkService.getUserById = function (userId) {
        return axiosInstance.get("/api/users/" + userId)
            .then(function (response) { return response.data.user; })
            .catch(function (err) { return Promise.reject(err); });
    };
    return NetworkService;
}());
export default NetworkService;



